"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { 
  Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter 
} from "@/components/ui/dialog"
import { 
  Plus, Play, Square, Slack, LogOut, Activity, 
  Sparkles, UserCheck, Eye, ShieldCheck, Zap, Info, Send, ChevronLeft, ChevronRight, Loader2, Clock, Users, Trophy, Layers
} from "lucide-react"

// --- 인터페이스 ---
interface Promotion {
  id: string; 
  name: string;        
  pointAmount: number; 
  totalStock: number;  
  startTime: string;   
  endTime: string;     
  status: "SCHEDULED" | "ACTIVE" | "ENDED";
  createdAt: string;
}

// 참여 현황 응답 인터페이스 추가
interface PromotionStatus {
  queueSize: number;
  participantCount: number;
  winnerCount: number;
  totalStock: number;
  remainingStock: number;
}

interface AdvisorRequest {
  userId: string;
  name: string;
  email: string;
  appliedAt: string;
  reason: string;
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [userRole, setUserRole] = useState<string | null>(null)
  const [userName, setUserName] = useState("")

  const PROMO_API = "http://34.50.7.8:3000/v1/promotions"
  const USER_API = "http://34.50.7.8/v1/user"

  const [promotions, setPromotions] = useState<Promotion[]>([])
  const [advisorRequests, setAdvisorRequests] = useState<AdvisorRequest[]>([])
  const [selectedRequest, setSelectedRequest] = useState<AdvisorRequest | null>(null)
  
  // 참여 현황 관련 상태
  const [statusData, setStatusData] = useState<PromotionStatus | null>(null)
  const [isStatusOpen, setIsStatusOpen] = useState(false)
  const [selectedPromoName, setSelectedPromoName] = useState("")

  const [isDetailOpen, setIsDetailOpen] = useState(false)
  const [isPromoCreateOpen, setIsPromoCreateOpen] = useState(false)
  const [promoPage, setPromoPage] = useState(0)
  const [totalPromoPages, setTotalPromoPages] = useState(1)

  // --- 프로모션 현황 조회 API 호출 ---
  const fetchPromotionStatus = async (id: string, name: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      setSelectedPromoName(name)
      const res = await fetch(`${PROMO_API}/${id}/status`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setStatusData(data.result)
        setIsStatusOpen(true)
      }
    } catch (error) {
      alert("현황 정보를 불러오는 데 실패했습니다.")
    }
  }

  const fetchPromotions = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      setIsLoading(true)
      const res = await fetch(`${PROMO_API}?page=${promoPage}&size=5`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setPromotions(data.result.content)
        setTotalPromoPages(data.result.totalPages)
      }
    } catch (e) { console.error(e) } finally { setIsLoading(false) }
  }, [promoPage])

  const fetchPendingAdvisors = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${USER_API}/admin/advisor/pending`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) setAdvisorRequests(data.result)
    } catch (e) { console.error(e) }
  }, [])

  useEffect(() => {
    const role = localStorage.getItem("role")
    const name = localStorage.getItem("name") || "관리자"
    if (role !== "MANAGER" && role !== "MASTER") { router.push("/login"); return; }
    setUserRole(role); setUserName(name); setIsMounted(true);
    fetchPromotions(); fetchPendingAdvisors();
  }, [router, fetchPromotions, fetchPendingAdvisors])

  const handleTogglePromotion = async (id: string, currentStatus: string) => {
    const token = localStorage.getItem("accessToken")
    const action = currentStatus === "SCHEDULED" ? "start" : "end"
    if (!confirm("상태를 변경하시겠습니까?")) return
    try {
      const res = await fetch(`${PROMO_API}/${id}/${action}`, {
        method: "PATCH",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) fetchPromotions()
    } catch (e) { alert("오류 발생") }
  }

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-background text-foreground font-sans">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={120} height={32} className="h-8 w-auto" />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-bold">{userRole} ADMIN</Badge>
          </Link>
          <div className="flex items-center gap-4">
            <p className="text-sm font-bold">{userName}님</p>
            <Button variant="ghost" size="icon" onClick={() => {localStorage.clear(); router.push("/login")}} className="rounded-xl"><LogOut className="h-5 w-5" /></Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        {/* 상단 요약 생략 (기본 구조 동일) */}
        <div className="flex justify-between items-end mb-8">
            <h1 className="text-3xl font-bold tracking-tighter uppercase font-black italic">Dashboard</h1>
        </div>

        <Tabs defaultValue="promotions">
          <TabsList className="bg-secondary/30 mb-6 p-1 rounded-xl">
            <TabsTrigger value="promotions" className="font-bold rounded-lg px-8 uppercase italic">Promotions</TabsTrigger>
            <TabsTrigger value="advisors" className="font-bold rounded-lg px-8 uppercase italic">Advisors</TabsTrigger>
          </TabsList>

          <TabsContent value="promotions">
            <Card className="p-6 border-border shadow-none rounded-2xl bg-card/50">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-black italic uppercase flex items-center gap-2"><Sparkles className="h-5 w-5 text-primary" /> Campaign Status</h2>
                <Button size="sm" onClick={() => setIsPromoCreateOpen(true)} className="rounded-xl font-bold"><Plus className="h-4 w-4 mr-1" /> 신규 등록</Button>
              </div>

              <div className="overflow-x-auto">
                {isLoading ? <div className="py-20 flex justify-center"><Loader2 className="animate-spin text-primary" /></div> : (
                  <table className="w-full text-left">
                    <thead className="text-muted-foreground text-[10px] font-black uppercase border-b border-border/50 tracking-widest">
                      <tr>
                        <th className="px-2 py-3">Promotion</th>
                        <th className="px-2 py-3">Duration</th>
                        <th className="px-2 py-3">Created</th>
                        <th className="px-2 py-3 text-center">Status</th>
                        <th className="px-2 py-3 text-right">Action</th>
                      </tr>
                    </thead>
                    <tbody className="divide-y divide-border/30 text-sm">
                      {promotions.map((p) => (
                        <tr key={p.id} className="group hover:bg-secondary/10 transition-all">
                          <td className="px-2 py-4">
                            <p className="font-black text-foreground italic uppercase text-base">{p.name}</p>
                            <p className="text-xs text-primary font-bold font-mono tracking-tighter">{p.pointAmount.toLocaleString()}P</p>
                          </td>
                          <td className="px-2 py-4">
                            <p className="text-[10px] font-mono text-muted-foreground leading-tight">S: {p.startTime.replace('T', ' ')}<br/>E: {p.endTime.replace('T', ' ')}</p>
                          </td>
                          <td className="px-2 py-4 font-mono text-[10px] text-muted-foreground">{p.createdAt.split('T')[0]}</td>
                          <td className="px-2 py-4 text-center">
                            <Badge variant={p.status === "ACTIVE" ? "default" : "secondary"} className="rounded-md font-black uppercase text-[9px]">{p.status}</Badge>
                          </td>
                          <td className="px-2 py-4 text-right flex justify-end gap-1">
                            {p.status === "SCHEDULED" && (
                              <Button size="icon" variant="ghost" className="h-8 w-8 text-green-600" onClick={() => handleTogglePromotion(p.id, "SCHEDULED")}><Play className="h-4 w-4 fill-current" /></Button>
                            )}
                            {p.status === "ACTIVE" && (
                              <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive" onClick={() => handleTogglePromotion(p.id, "ACTIVE")}><Square className="h-4 w-4 fill-current" /></Button>
                            )}
                            {/* 상세 현황 조회 버튼 (Info 클릭 시 실행) */}
                            <Button size="icon" variant="ghost" className="h-8 w-8 text-primary hover:bg-primary/10" onClick={() => fetchPromotionStatus(p.id, p.name)}><Info className="h-4 w-4" /></Button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                )}
              </div>
            </Card>
          </TabsContent>
        </Tabs>
      </main>

      {/* --- 프로모션 참여 현황 모달 (추가된 기능) --- */}
      <Dialog open={isStatusOpen} onOpenChange={setIsStatusOpen}>
        <DialogContent className="sm:max-w-[500px] rounded-[2rem] border-none p-8 bg-card shadow-2xl">
          <DialogHeader>
            <DialogTitle className="font-black italic text-2xl uppercase tracking-tighter flex items-center gap-2">
              <Activity className="h-6 w-6 text-primary" /> Live Status
            </DialogTitle>
            <p className="text-sm font-bold text-muted-foreground uppercase italic">{selectedPromoName}</p>
          </DialogHeader>

          {statusData && (
            <div className="grid grid-cols-2 gap-4 py-6">
              <div className="p-4 bg-secondary/20 rounded-2xl border border-border/50">
                <p className="text-[10px] font-black uppercase text-muted-foreground mb-1 flex items-center gap-1"><Layers className="h-3 w-3" /> Queue Size</p>
                <p className="text-2xl font-black font-mono">{statusData.queueSize.toLocaleString()}</p>
              </div>
              <div className="p-4 bg-secondary/20 rounded-2xl border border-border/50">
                <p className="text-[10px] font-black uppercase text-muted-foreground mb-1 flex items-center gap-1"><Users className="h-3 w-3" /> Participants</p>
                <p className="text-2xl font-black font-mono">{statusData.participantCount.toLocaleString()}</p>
              </div>
              <div className="p-4 bg-primary/10 rounded-2xl border border-primary/20">
                <p className="text-[10px] font-black uppercase text-primary mb-1 flex items-center gap-1"><Trophy className="h-3 w-3" /> Winners</p>
                <p className="text-2xl font-black font-mono text-primary">{statusData.winnerCount.toLocaleString()}</p>
              </div>
              <div className="p-4 bg-background rounded-2xl border border-border/50">
                <p className="text-[10px] font-black uppercase text-muted-foreground mb-1">Remaining / Total</p>
                <p className="text-lg font-black font-mono tracking-tighter">
                  <span className="text-primary">{statusData.remainingStock}</span> / {statusData.totalStock}
                </p>
              </div>
              
              {/* 진행률 바 */}
              <div className="col-span-2 mt-2">
                <div className="flex justify-between text-[10px] font-black uppercase mb-1">
                  <span>Progress</span>
                  <span>{Math.round(((statusData.totalStock - statusData.remainingStock) / statusData.totalStock) * 100)}%</span>
                </div>
                <div className="h-2 w-full bg-secondary rounded-full overflow-hidden">
                  <div 
                    className="h-full bg-primary transition-all duration-500" 
                    style={{ width: `${((statusData.totalStock - statusData.remainingStock) / statusData.totalStock) * 100}%` }}
                  />
                </div>
              </div>
            </div>
          )}

          <DialogFooter>
            <Button className="w-full font-black h-12 rounded-xl uppercase italic shadow-lg shadow-primary/20" onClick={() => setIsStatusOpen(false)}>Close Status</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}